<template>
  <div
    v-if="form_responses"
    id="responses"
    class="table-container box"
  >
    <table 
      class="table is-striped is-hoverable is-fullwidth" 
      style="border: 1px solid #dbdbdb;"
    >
      <caption class="mb-0 p-2">
        <span class="title">As minhas inscrições</span>
      </caption>
      <thead>
        <tr>
          <th
            v-if="!event"
            style="text-align: center; vertical-align: middle;"
          >
            Evento
          </th>
          <th
            style="text-align: center; vertical-align: middle;"
          >
            Estado
          </th>
          <th
            style="text-align: center; vertical-align: middle;"
          >
            Bilhete
          </th>
          <th
            style="text-align: center; vertical-align: middle;"
          >
            Certificado
          </th>
          <th
            style="text-align: center; vertical-align: middle;"
          >
            Feedback
          </th>
        </tr>
        <tr>
          <th 
            v-if="!event" 
            class="p-0"
          >
            <div class="field">
              <div class="control has-icons-left">
                <input
                  v-model="eventSearch"
                  type="text"
                  class="input is-info is-small m-0"
                  placeholder="Pesquisar por evento"
                >
                <span class="icon is-small is-left">
                  <i class="fas fa-search" />
                </span>
              </div>
            </div>
          </th>
          <th class="p-0">
            <div class="field">
              <div class="control has-icons-left">
                <input
                  v-model="statusSearch"
                  type="text"
                  class="input is-info is-small m-0"
                  placeholder="Pesquisar por estado (Submetida / Validada)"
                >
                <span class="icon is-small is-left">
                  <i class="fas fa-search" />
                </span>
              </div>
            </div>
          </th>
          <th />
          <th />
          <th />
        </tr>
      </thead>
      <tbody v-if="form_responses">
        <tr v-if="form_responses.length == 0">
          <td
            colspan="5"
            class="subtitle"
            align="center"
          >
            Não propôs nenhum evento.
          </td>
        </tr>
        <tr
          v-for="(response, responseIndex) in filteredResponses()"
          :key="responseIndex"
        >
          <td v-if="!event">
            {{ response.eventName }}
          </td>
          <td
            v-if="statuses"
            style="vertical-align: middle;"
            align="center"
          >
            <span
              v-if="loading_status[response.id] === 'error'"
              class="is-danger connection-error"
            >
              Houve um erro de conexão. Por favor recarregue a página.
            </span>
            <div
              v-else
              class="flex column vca p-0"
            >
              <a
                v-if="statuses?.[answerStatuses[response.id]].name=='Cancelada'"
                class="mb-2 is-size-5 flex row vca"
                :href="'/forms/respond/'+response.form+'?event='+response.event"
              >Cancelada (Editar)</a>
              <span
                v-else
                class="mb-2 is-size-5 flex row vca"
              ><span class="mr-2">{{ statuses?.[answerStatuses[response.id]].name }}</span>
              </span>
            </div>
          </td>
          <td v-else>
            A carregar...
          </td>
          <td align="center">
            <a
              v-if="(response.status == 2 || response.status == 4) && response.ticketAvailable"
              class="button is-primary"
              :href="'/forms/ticket/?event='+response.event"
            >Bilhete</a>
          </td>
          <td align="center">
            <a
              v-if="response.status == 4 && response.certificateAvailable"
              class="button is-primary"
              :href="'/forms/certificate/?event='+response.event"
            >Certificado</a>
          </td>
          <td align="center">
            <a
              v-if="response.status == 4 && response.feedbackForm"
              class="button is-primary"
              :href="'/forms/respond/'+response.feedbackForm+'?event='+response.event"
            >Submeter feedback</a>
          </td>
        </tr>
      </tbody>
      <tbody v-else>
        <tr style="height: 50px;">
          <td />
          <td v-if="!event" />
          <td />
        </tr>
        <tr style="height: 50px;">
          <td />
          <td v-if="!event" />
          <td />
        </tr>
        <tr style="height: 50px;">
          <td />
          <td v-if="!event" />
          <td />
        </tr>
        <tr style="height: 50px;">
          <td />
          <td v-if="!event" />
          <td />
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import * as components from "@/fields";
import { API } from "@/http-common";
import { getCookie, timeSince, stringIn } from "@/common";
export default {
  name: "ViewAllResponses",
  components,
  data() {
    return {
      form_responses: undefined,
      displayed_sections: [],
      event: undefined,
      user: undefined,
      statuses: undefined,
      eventSearch: "",
      statusSearch: "",
      answerStatuses: [],
      interval : undefined,
      loading_status : {}
    };
  },
  created() {
    document.title = "As minhas isncrições"
    var queryString = "myEvents/?type=1";
    API.get(queryString)
      .then(async (response) => {
          this.form_responses = response.data
        }
      );

    API.get("answerStatus/")
      .then((response) => {
        this.statuses = response.data;
      });
    this.getStatuses();
  },
  methods: {
    getStatuses() {
      API.get("formAnswerStatus/")
      .then((response) => {
        this.answerStatuses = response.data;
      });
    },
    changeStatus(answer, status) {
      this.loading_status[answer] = 'loading';
      setTimeout(this.patchStatus.bind(this, answer, status), 0);
    },
    patchStatus(answer, status) {
      const options = {
        method: "patch",
        url: "/formAnswerStatus/" + answer + "/",
        headers: {
          "X-CSRFToken": getCookie("csrftoken"),
        },
        data: {
          "status": status
        },
        transformResponse: [
          (data) => {
            this.response = data;
            return data;
          },
        ],
      };
      API(options)
        .then(() => this.loading_status[answer] = undefined)
        .catch((error) => {
          console.log(error); 
          this.loading_status[answer] = 'error';
          this.answerStatuses[answer] = 'error';
        });
    },
    allFalse() {
      for (var i = 0; i < this.displayed_sections.length; i++)
        if (this.displayed_sections[i] == true)
          return false;
      return true;
    },
    filteredResponses() {
      var result = this.form_responses;
      if (this.eventSearch != "")
        result = result.filter(x => {return x.eventName ? stringIn(x.eventName, this.eventSearch) : false});
      if (this.statusSearch != "")
        result = result.filter(x => stringIn(this.statuses[this.answerStatuses[x.id]].name, this.statusSearch));
      return result;
    },
    timeSince: timeSince
  }
};
</script>

<style lang="scss">
  @import '@/styles/flex-styles.scss';
  @import '@/styles/global-styles.scss';
</style>